/* Desktop View */
.navbar{
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.16em;
  color: #000000;
  width: 90%;
}
  .login-btn{
    background-color: whitesmoke;
    color: black;
    padding: 10px 26px;
    background: #FFFFFF;
    border: 3px solid #4A4A4A;
    border-radius: 300px;
  }
  .desktop-menu {
      display: block;
  }
  div.banner .mobile-menu {
      display: none;
  }
  .navbar{
      width: 90%;
      margin: auto;
      display: flex;
      align-items: center;
      justify-content:  space-between;
    }
    .logo{
        width:  120px;
        cursor: pointer;
        margin-left:50px;
        width: 120px;
        cursor: pointer;
        margin-left: 50px;
        height: 100px;
        width: 200px;
    }
    .navbar ul li{
        list-style: none;
        display: inline-block;
        padding: 0px 20px;
        position: relative;
        cursor: pointer;
    }
    .navbar ul li a{
        text-decoration: none;
        color: black;
        text-transform: uppercase;
        font-size: 16px;
        font-weight: 500;
        cursor: pointer;
    }
    .navbar ul li::after {
        content: '';
        height:  3px;
        width: 0%;
        background: #009688;
        position: absolute;
        left: 0;
        bottom: -10px;
        transition: 0.5s;
        cursor: pointer;
    }
    .navbar ul li:hover::after {
        width: 100%;
    }
    .navbar-nav {
      text-align: center;
    }

@media only screen and (min-width: 981px) and (max-width: 1370px) { 
  .navbarul li{
    list-style: none;
    display: inline-block;
    padding: 0px 10px;
    position: relative;
    cursor: pointer;
  }
  .navbar ul li {
    padding: 0px 2px;
    font-style: normal;
    font-weight: 500;
    font-size: 10px;
    line-height: 13px;
    letter-spacing: 0.16em;
  }
  .navbar ul li a{
    font-size: 14px;
  }
  .logo{
    width: 188px;
  }
}

/* Tab View - 769px */
@media only screen and (min-width: 765px) and (max-width: 980px) {
  
    .navbar {
      width: 88%;
      margin: 0px 10px 0px 20px;
    }
    .navbar ul li {
        padding: 0px 2px;
        font-style: normal;
        font-weight: 500;
        font-size: 10px;
        line-height: 13px;
        letter-spacing: 0.16em;
      }
      .logo {
        width: 120px;
        cursor: pointer;
        margin-left: 0px;
        height: 100px;
        width: 120px;
        margin-top: -20px;
      }
      .desktop-menu{
        padding-left: 0px;
      }
      .navbar ul li a{
        font-size: 12px;
      }
    }

  /* Mobile View - 376px */
  @media only screen and (max-width: 767px){
        .navbar ul li{
            margin: 20px 0px;
        }
        li.nav-item{
          margin-left: 120px;
        }
        .navbar-toggler{
          float: right;
        }
        span.close {
          display: none;
        }
        button[aria-expanded="true"] > span.close {
          display: inline;
          color: #fff;
          opacity: 1;
        }
        button[aria-expanded="true"] > span.navbar-toggler-icon {
          display: none;
        }
        button[aria-expanded="false"] > span.close {
          display: none;
        }
        .hamburger{
          display: block;
        }
        .desktop-menu {
          display: none;
        }
        div.banner .mobile-menu {
          display: block;
        }
        div.banner .mobile-menu li:hover {
          background-color: #80808052;
        }
        hr{
          margin-top: 10px;
          margin-bottom: 0px;
          border-top: 2px solid white;
        }
  }